.footer{
	position: absolute;
	bottom:0; left:0; right:0;
	margin:0 auto;
	width:100%; // height:85px;
	background:#363636;
	color:#fff;
	padding: 34px 20px;
	.copyright{
		@extend .float-left;
	}
	.footer-menu{
		@extend .clearfix;
		@extend .float-right;
		margin:0;
		li{
			@extend .float-left;
			border-right:1px solid #fff;
			&:last-child{
				border-right:0;
			}
			a{
				display: block;
				padding: 0 15px;
			}
		}
	}
}

